<template>
  <div class="main-div">
    <br />
    <b-col offset="1">
      <h4>
        <div>Closing month</div>
      </h4>
      <h6>
        <div>->> Post data to Loan</div>
        <div>->> Post data to leave history</div>
        <div>->> Post data to Month End History</div>
        <div>->> Close month</div>
      </h6>
    </b-col>
    <b-row class="mt-1">
      <b-col cols="2" offset="1">
        <b-button class="button-style" variant="success" @click="closeMonth"
          >Submit</b-button>
      </b-col>
    </b-row>
    <br><br><br>
    <b-col offset="1">
    <h4>
        <div>Update Gosi</div>
      </h4>
      <h6>
        <div>->> Update Gosi</div>
      </h6>
    </b-col>
    <b-row class="mt-1">
      <b-col cols="2" offset="1">
        <b-button class="button-style" variant="success" @click="updateGosi"
          >Submit</b-button>
      </b-col>
    </b-row>
    <div v-show="isBusy">
      <template>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle"></b-spinner>
          <h4><strong>Processing, please wait...</strong></h4>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import axios from 'axios'
export default {
  data () {
    return {
      isBusy: false,
      confirmAction: false
    }
  },
  computed: {
    ...mapGetters([]),
    total: function () {
      return parseFloat(this.t1 || 0) + parseFloat(this.t2 || 0)
    }
  },
  methods: {
    ...mapActions([]),
    closeMonth () {
      this.$bvModal.msgBoxConfirm('Please confirm that you want to continue with Close Month procedure.', {
        title: 'Please Confirm',
        size: 'sm',
        buttonSize: 'sm',
        okVariant: 'danger',
        okTitle: 'YES',
        cancelTitle: 'NO',
        footerClass: 'p-2',
        hideHeaderClose: false,
        centered: true
      })
        .then(value => {
          // this.boxTwo = value
          if (value) {
            this.isBusy = !this.isBusy
            this.processCloseMonth()
          } else {
            alert('Close month aborted')
          }
        })
        .catch(err => {
          // An error occurred
          alert(err)
        })
      // this.processCloseMonth()
    },
    updateGosi () {
      this.$bvModal.msgBoxConfirm('Please confirm that you want to continue update gosi procedure.', {
        title: 'Please Confirm',
        size: 'sm',
        buttonSize: 'sm',
        okVariant: 'danger',
        okTitle: 'YES',
        cancelTitle: 'NO',
        footerClass: 'p-2',
        hideHeaderClose: false,
        centered: true
      })
        .then(value => {
          // this.boxTwo = value
          if (value) {
            this.isBusy = !this.isBusy
            this.processUpdateGosi()
          } else {
            alert('Update gosi aborted')
          }
        })
        .catch(err => {
          // An error occurred
          alert(err)
        })
      // this.processCloseMonth()
    },
    async processCloseMonth () {
      // console.log('Closing month')
      const url = process.env.VUE_APP_API_URL + '/monthEndClose'
      await axios
        .post(url, {
          headers: {
            'Content-Type': 'application/json'
          }
        })
        .then(response => {
          this.msg = {}
          //   commit('setMessage', 'Sucess')
          this.isBusy = !this.isBusy
          this.$toasted.show('Close month process completed sucessfully!', {
            theme: 'bubble',
            position: 'top-right',
            duration: 5000
          })
        })
        .catch(error => {
          console.log(error.response.data)
          this.msg = 'Error:' + JSON.stringify(error.response.data.errorMessage)
          this.$toasted.show('Error in processing Close month!', {
            theme: 'bubble',
            position: 'top-right',
            duration: 5000
          })
          this.isBusy = !this.isBusy
        })
    },
    async processUpdateGosi () {
      // console.log('Closing month')
      const url = process.env.VUE_APP_API_URL + '/updateGosi'
      await axios
        .post(url, {
          headers: {
            'Content-Type': 'application/json'
          }
        })
        .then(response => {
          this.msg = {}
          //   commit('setMessage', 'Sucess')
          this.isBusy = !this.isBusy
          this.$toasted.show('Update GOSI process completed sucessfully!', {
            theme: 'bubble',
            position: 'top-right',
            duration: 5000
          })
        })
        .catch(error => {
          console.log(error.response.data)
          this.msg = 'Error:' + JSON.stringify(error.response.data.errorMessage)
          this.$toasted.show('Error in processing update gosi!', {
            theme: 'bubble',
            position: 'top-right',
            duration: 5000
          })
          this.isBusy = !this.isBusy
        })
    }
  }
}
</script>

<style scoped>
.main-div {
  width: 95%;
  margin-left: 40px;
  margin-right: 40px;
  font-size: small;
}
.center {
  text-align: center;
  /* border: 3px solid green; */
}
.col-right {
  text-align: right;
}
.col-left {
  text-align: left;
}
.div-border {
  border: 5cm;
}
input:invalid {
  border-color: red;
  background-color: rgb(243, 243, 203);
}
input:valid {
  border-color: green;
}
.error-message {
  display: block;
}
</style>
